















































































export default {
	
	data(){
		return{
			show:true,
			timer:null,
			show1:true,
			timer1:null,
			count1:'',
			count:'',
			type:1,
			user:"",//账号
			pwd:'',//密码
			phone:'',//手机号码
			code:'',//验证码
			havePwd:true,
			
		}
	},
	methods:{
		getCode(){
			this.show = false;
			const TIME_COUNT = 60;      
			if (!this.timer) {        
				this.count = TIME_COUNT;        
				this.show = false;        
				this.timer = setInterval(() => {        
				if (this.count > 0 && this.count <= TIME_COUNT) {          
					this.count--;         
				} else {          
					this.show = true;          
					clearInterval(this.timer);          
						this.timer = null;         
					}        
				}, 1000)       
			}    
		},
		getCode1(){
			this.show1 = false;
			const TIME_COUNT = 60;      
			if (!this.timer1) {        
				this.count1 = TIME_COUNT;        
				this.show1 = false;        
				this.timer1 = setInterval(() => {        
				if (this.count1 > 0 && this.count1 <= TIME_COUNT) {          
					this.count1--;         
				} else {          
					this.show1 = true;          
					clearInterval(this.timer1);          
						this.timer1 = null;         
					}        
				}, 1000)       
			}    
		},
		tabChange(){
			if(this.type==1){
				this.type=2
			}else{
				this.type=1
			}
		},
		// 对密码看见
		showPwd(){
			if(this.pwd.trim()==''){
				uni.showToast({
					title: '请输入密码!',
					duration:2000,
					icon:'none'
				});
				return;
			}
			this.havePwd=!this.havePwd;
		},
		// 点击可以删除账号
		showUser(){
			this.user=''
		},
		// d点击删除手机号
		delPhone(){
			this.phone=''
		},
		// 点击按钮开始登录
		loginCode(){
			// 验证码登录
			var phoneReg=/^1[345789]\d{9}$/;
			if(this.type==1){
				if(this.phone.trim()==''&&this.code.trim()==''){
					uni.showToast({
						title: '请将内容填写完整!',
						duration:2000,
						icon:'none'
					});
					return;
				}else if(!(phoneReg).test(this.phone)){
					uni.showToast({
						title: '您输入的的手机号码有误!',
						duration:2000,
						icon:'none'
					});
					return;
				}else if(this.phone.trim()==''){
					uni.showToast({
						title: '手机号码不能为空!',
						duration:2000,
						icon:'none'
					});
					return;
				}else if(this.code.trim()==''){
					uni.showToast({
						title: '验证码不能为空!',
						duration:2000,
						icon:'none'
					});
					return;
				}else if(this.code.trim().length<6){
					uni.showToast({
						title: '您输入的的验证码有误!',
						duration:2000,
						icon:'none'
					});
					return;
				}else{
					uni.redirectTo({
						url:'../../myCenter/myOrder'
					})
				}
			}else{
				if(this.user.trim()==''&&this.pwd.trim()==''){
					uni.showToast({
						title: '请将内容填写完整!',
						duration:2000,
						icon:'none'
					});
					return;
				}else if(this.user.trim()==''){
					uni.showToast({
						title: '您输入的的账号不能为空!',
						duration:2000,
						icon:'none'
					});
					return;
				}else if(this.pwd.trim()==''){
					uni.showToast({
						title: '您输入的的密码不能为空!',
						duration:2000,
						icon:'none'
					});
					return;
				}else{
					uni.redirectTo({
						url:'../../myCenter/myOrder'
					})
				}
			}
			
		},
	}
}



